$input-group-addon-bg: $link;

.input-group:focus-within .input-group-text, .form-control:focus ~ .input-group-text {
    box-shadow: -2px 0px 15px 0px rgba(187, 187, 187, 0.5) !important;
}

.input-group .form-control:focus{
    box-shadow: 5px 0px 12px 0px rgba(187, 187, 187, 0.5) !important;
}

